*{
  box-sizing: border-box;
}
.app {
  min-height: 100vh;
  padding:1em;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.app__fraction{
  width: 100%;
  /* height:30vh; */
  box-sizing: border-box;
}
.fraction__item{
  width: 100%;
  display:flex;
  height:6vh;
  text-align:center;
  align-items: center;
  justify-content: center;
  font-size:1.2em;
}
.fraction__item:first-child{
  font-weight: bold;
  font-size:1.3em;
  margin:0;
}
.fraction__item_main{
  flex:14.28%
}
.primaty{
  font-weight: bold;
  color:#4b8718;
}
.app__menu{
  padding:1em 0;
}
.app__menu_btn{
  display:flex;
  align-items:center;
  justify-content:space-between;
}

.app__menu_operation{
  padding:1em 0;

}
.item_title{
  padding:1em 0;
  font-size:1.2em;
}
.app__menu_submit{
  display:flex;
  align-items:center;
  justify-content:center;
  margin:10em;
}